import Breadcrumbs from '~/components/navigation/Breadcrumbs.vue'
import ImageCarousel from '~/components/carousels/ImageCarousel.vue'
import GroupLinks from '~/components/navigation/GroupLinks.vue'
import Contacts from '~/components/contacts/Contacts.vue'
import MapCard from '~/components/informative/MapCard.vue'
import SimpleContent from '~/components/text-elements/SimpleContent.vue'
import TopImage from '~/components/informative/TopImage.vue'
import common from '~/mixins/common'

export default {
  name: 'SingleEventPage',
  components: {
    TopImage,
    SimpleContent,
    ImageCarousel,
    MapCard,
    Contacts,
    Breadcrumbs,
    GroupLinks,
  },
  mixins:[common],
  async asyncData({ route, $axios, error }) {
    try {
      const { title } = route.params
      const { data } = await $axios.get('/api/events/' + title)

      return {
        data,
      }
    } catch (e) {
      error({ statusCode: 404, message: 'Page not found' })
    }
  },
  head() {
    return {
      title: this.title,
      meta: [
        {
          hid: 'description',
          name: 'description',
          content:
            'Information about ' +
            this.data.title +
            ', event that takes place at ' +
            this.data.poi.title,
        },
      ],
    }
  },
  mounted() {
    const groupLinks = this.retriveGroupLinks()

    const pageLinks = [{
      title: this.$data.data.poi.title,
      linkPath: '/point-of-interest/' + this.$data.data.poi.title.replaceAll(' ', '-'),
    }]

    groupLinks[2].links = pageLinks

    sessionStorage.setItem('groupLinks', JSON.stringify(groupLinks))
  },
  methods: {
    backToEvents() {
      this.$router.push('/events')
    },
    formatArray(images) {
      const formatted = []
      for (const item of images.slice(0)) {
        formatted.push({
          img: item.path,
        })
      }
      return formatted
    },
    handleNull(address) {
      if (address != null) return address
      return this.data.poi.title.concat(', Venezia')
    },
  },
}
