import common from '~/mixins/common.js'

export default {
  name: 'ContactsComponent',
  mixins: [common],
  props: {
    title: {
      type: String,
      default: 'More information',
      required: false,
    },
    date: {
      type: String,
      default: null,
      required: false,
    },
    openingHour: {
      type: String,
      default: 'none',
      required: false,
    },
    closingHour: {
      type: String,
      default: 'none',
      required: false,
    },
    ticket: {
      type: Number,
      default: 9999,
      required: false,
    },
    landlinePhone: {
      type: String,
      default: 'none',
      required: false,
    },
    mobilePhone: {
      type: String,
      default: 'none',
      required: false,
    },
    email: {
      type: String,
      default: 'none',
      required: false,
    },
  },
}
