export default {
  name: 'ImageCarousel',
  props: {
    title: {
      type: String,
      required: true,
    },
    elementsToShow: {
      // contains only elements with img attribute with the path to show
      type: Array,
      required: true,
    },
    numOfCarousel: {
      // numOfCarousel rappresent the index of carousel in the page, must be different in each carousel to make it work in page
      type: Number,
      required: true,
    },
  },
  data() {
    const myLenght = this.elementsToShow.length
    return {
      myLenght,
    }
  },
  computed: {
    amountOfindicatorsNeeded() {
      return this.myLenght
    },
  },
}
