export default {
  name: 'MapCard',
  props: {
    title: {
      type: String,
      required: false,
      default: null,
    },
    address: {
      type: String,
      required: false,
      default: null,
    },
    showButton: {
      type: Boolean,
      required: false,
      default: true,
    },
    webLink: {
      type: String,
      required: false,
      default: null,
    },
    zoom:{
      type: Number,
      required: false,
      default: 15,
    }
  },
  methods: {
    discoverMore() {
      this.$router.push('/points-of-interest/' + this.titleToLink(this.title))
    },
    titleToLink(title) {
      return title.replaceAll(' ', '-')
    },
  },
}
